@import url(https://fonts.googleapis.com/css?family=Bangers&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-gradient(linear, right top, left top, from(rgb(38, 5, 129)), to(rgb(63, 6, 167)));
  background: linear-gradient(to left, rgb(38, 5, 129) 0%, rgb(63, 6, 167) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-family: 'Bangers', cursive;
  font-weight: 200;
  /* color: #000000; */
  background: -webkit-linear-gradient(rgb(13, 161, 137), rgb(6, 233, 195));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.f-headline {
  margin: 0;
  margin-top: 0.2em;
  
}

.slogan {
  font-family: 'Bangers', cursive;
  font-size: x-large;
  letter-spacing: 2px;
  margin: 0;
  margin-bottom: 15px;
  color: white;
}
